import React from "react"
import "./styles.css"

const Banner = () => (
  <section className="container banner" style={{background: 'url(img/bg.jpg)'}}>
    <div className="banner-logo item">
        <img src="img/logo-big.png" alt="Volstar" />
    </div>
    <div className="banner-slogan item">
        <h1>Soluciones de almacenamiento de energía</h1>
        <h2>Desarrollo de soluciones customizadas</h2>
    </div>
    
  </section>
)


export default Banner
