import React from "react"
import Input from "./input"
import Textarea from "./textarea"
import Button from "./button"

import "./styles.css"

const Contact = (props) => {
 let {sendEmail, handleInputChange} = props

  return (
    <section className="contact container">
        
        <div className="title color-white">contáctanos</div>
        
        <form id="formul" onSubmit={sendEmail}> 
            <div className="item">
              <input name="nombre" placeholder="Nombre" onChange={handleInputChange} />
            </div>
            
            <Input idinput="telefono" text="Teléfono" valor="" handleInputChange={handleInputChange} />
            <Input idinput="correo" text="Correo" valor="" handleInputChange={handleInputChange} />
            <Input idinput="asunto" text="Asunto" valor="" handleInputChange={handleInputChange} />   
            <Textarea idinput="mensaje" text="Mensaje" valor="" handleInputChange={handleInputChange} />  
            <div className="item col-right">
                <Input idinput="interes" text="Segmento de interés" valor="" handleInputChange={handleInputChange} />  
                <Button idinput="button" type="submit" text="Enviar"  />
            </div>
        </form>
           
    </section>
  )
}


export default Contact
